import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import orderList from "../view/orderList.vue";
import { findData, intoData } from '@/store/local';
import { checkToken } from "@/api/api";


const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'index',
    component: () => import('../views/index.vue'),

    children: [
      {
        path: '/',
        component: () => import("../views/backgroundHomepage.vue"),
        meta: {
          title: '主页',
        }
      },
      {
        path: "/orderList",
        component: () => import("../views/orderList.vue"),
        meta: {
          title: '订单管理',
        }
      },
      {
        path: "/productList",
        component: () => import("../views/productList.vue"),
        meta: {
          title: '商品管理',
        }
      },
      {
        path: "/adminUser",
        component: () => import("../views/adminUser.vue"),
        meta: {
          title: '管理员管理',
        }
      },
      {
        path: "/user",
        component: () => import("../views/user.vue"),
        meta: {
          title: '用户管理',
        }
      }, {
        path: "/user/profile",
        component: () => import("../views/profile.vue"),
        meta: {
          title: '个人信息管理',
        }
      }, {
        path: "/tadetype",
        component: () => import("../views/tadetype.vue"),
        meta: {
          title: '商品类型管理',
        }
      }, {
        path: "/rotationChart",
        component: () => import("../views/rotationChart.vue"),
        meta: {
          title: '轮播图管理',
        }
      }, {
        path: "/coupon",
        component: () => import("../views/coupon.vue"),
        meta: {
          title: '优惠券管理',
        }
      }, {
        path: "/userLoginLog",
        component: () => import('../views/userLoginLog.vue'),
        meta: {
          title: '用户登陆管理',
        }
      }
    ]

  }, {
    path: '/login',
    name: 'login',
    component: () => import('../views/login.vue'),
    meta: {
      title: '登陆',
    }
  },
  {
    path: '/install',
    name: 'install',
    component: () => import('../views/install.vue'),
    meta: {
      title: '系统初始化',
    }
  },
  {
    path: '/:pathMatch(.*)',
    redirect: '/'
  }


]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})



router.beforeEach(async (to, from, next) => {

  if (to.meta.title) { // 判断是否有标题
    document.title = "鞋来鞋往-" + to.meta.title;
  }

  const parr = ['login', 'install']

  //存储更新信息
  if (!parr.includes(to.name as string)) {
    const { data } = await checkToken();
    const token = findData('token');
    intoData(data);

    if (token) {

      next();
    }
  }
  else {

    if (to.name == 'login' && findData('token')) {
      next({ name: from.name || 'index' });
    } else {
      next();
    }

  }
})
export default router
