import axios, { AxiosInstance, AxiosRequestConfig } from 'axios'
import router from '@/router'
import { findData, delData } from '@/store/local'
import { ElMessage } from 'element-plus'
/* eslint-disable */


interface responDataInterface<T> {
    data: T,
    msg: string
    code: number
}

const toLogin = () => {
    delData('token', 'uname')
    router.replace({
        path: '/login'
    })
}

class Request {
    private instance: AxiosInstance | undefined

    constructor(requeseConfig: AxiosRequestConfig) {
        // requeseConfig.baseURL = "/api";
        this.instance = axios.create(requeseConfig)

        // 全局请求拦截
        this.instance.interceptors.request.use(
            (config: any) => {
                config.headers.Authorization = findData('token');
                return config
            },
            (error) => {

                Promise.reject(error)
            },
        )

        // 全局响应拦截
        this.instance.interceptors.response.use(
            (res) => {


                // res 为AxiosResponse 类型，含有conig\data\headers\request\status\statusText属性
               
                    const code = res.data.code;
                    //使用公共常量提取
                    if (code == 200) {
                        // ElMessage.success(res.data.msg);
                        return res
                    }
                    else if (code == 400) {
                        ElMessage.error(res.data.msg);
                        return res;
                    } else if (code == 500) {
                        ElMessage.error(res.data.msg);
                        return res;
                    }
                    else if (code == 201) {
                        ElMessage.error(res.data.msg);
                        return res;
                    }

                    return Promise.reject(res.data);
                    // 只需要返回data即可

               
            },
            (error) => {


                let message = ''
                if (error.message == 'Network Error') {
                    message = '未连接服务器'

                }

                // 请求超时
                if (error.message == 'timeout of 60000ms exceeded') {
                    message = '请求超时'
                }

                switch (error.response.status) {
                    case 400:
                        message = "请求错误(400)";
                        break;
                    case 401:
                        message = "未授权，请重新登录(401)";
                        toLogin();
                        // 这里可以做清空storage并跳转到登录页的操作
                        break;
                    case 403:
                        message = "拒绝访问(403)";
                        break;
                    case 404:
                        message = "请求出错(404)";
                        break;
                    case 408:
                        message = "请求超时(408)";
                        break;
                    case 500:
                        message = "服务器错误(500)";
                        break;
                    case 501:
                        message = "服务未实现(501)";
                        break;
                    case 502:
                        message = "网络错误(502)";
                        break;
                    case 503:
                        message = "服务不可用(503)";
                        break;
                    case 504:
                        message = "网络超时(504)";
                        break;
                    case 505:
                        message = "HTTP版本不受支持(505)";
                        break;
                    default:
                        message = `连接出错(${error.response})!`;
                }

                ElMessage.error(message);
                return Promise.reject(message)

            },
        )
    }

    request<T>(config: AxiosRequestConfig): Promise<responDataInterface<T>> {
        return new Promise<responDataInterface<T>>((resolve, reject) => {
            this.instance?.request(config)
                .then((res) => {

                    const data: responDataInterface<T> = res.data
                    resolve(data);
                })
                .catch((err) => {
                    reject(err)
                })
        })
    }
}


export default Request