import { createStore } from 'vuex'

export const store = createStore({
  state: {
    user: {},
  },
  getters: {

  },
  //定义缓存方法
  mutations: {
    //添加user信息
    setUser(state, user) {
      state.user = user;
    },
    //删除user信息
    clearUser(state, user) {
      state.user = {}
    }
    //部分数据赋值
    
  },
  actions: {
  },
  modules: {
  }
})
